.main-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 8%
}
.span-el{
    text-decoration: line-through;
}
.typing-demo {
    width: 25ch;
    animation: typing 4s steps(25), blink .5s step-end infinite alternate;
    white-space: nowrap;
    overflow: hidden;
    border-right: 3px solid;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    font-size: 2em;
}

.college-container{
    margin-top: 45%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 5%;
}

.img-el{
    height: 30vh;
}

.clg-heading{
    margin-left: 8%;
}

@media screen and (max-width: 425px) {
  .typing-demo{
    font-size: 1em;
  }
  .img-el{
    height: 10vh;
  }
  .clg-heading{
    font-size: 20px;
    margin-left: 8%;
  }
}
  
  @keyframes typing {
    from {
      width: 0
    }
  }
      
  @keyframes blink {
    50% {
      border-color: transparent
    }
  }