.nav-bar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 10vh;
}
.list-container{
    list-style: none;
    display: flex;
    align-items: center;
    width: 20vw;
}
.list-el{
    margin-right: 8%;
}
.nav-item{
    margin-left: 5%;
    text-decoration: none;
    color: black;
    font-size: 18px;
}

@media screen and (max-width: 425px) {
    .nav-bar{
        width: 75vw;
    }
  }